html, body {
    height: 100%;
}

body {
    margin: 0;
}

#root {
    height: inherit;
}

.nav {
    margin: 10px;
    display: flex;
    justify-content: center;
}

.nav button {
    display: block;
    height: 70px;
    width: 70px;
    border: none;
    border-radius: 50%;
    background-color: #634747;
    color: white;
    font-size: 18px;
}

.content {
    display: flex;
    justify-content: center;
}

.container {
    display: flex;
    flex-wrap: wrap;
    width: 60%;
}

.card {
    margin: 5px 5px;
    width: 11%;
}

img {
    width: 100%;
    height: auto;
    padding: 2px 2px;
}